.loader {
    border: 16px solid #f3f3f3; /* Light grey */
    border-top: 16px solid black; 
    border-radius: 80%;
    width: 120px;
    height: 120px;
    margin: 0 auto;
    margin-top: 100px;
    animation: spin 1s linear infinite;
  }
  
  @keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
  }